import { MenuItem, TextField } from '@mui/material';
import { ErrorMessage, useField } from 'formik'
import React from 'react'
import { theme } from '../../configtheme';
import { styled } from '@mui/material/styles';

const CustomTextField = styled(TextField)`
    & label span{
        color:red!important
    }
    & input:disabled{
        -webkit-text-fill-color: rgba(0, 0, 0, 1.0);
    }
`

export const InputSelect = ({ label, ...props }) => {
    const [field, meta] = useField(props);
    return (
        <>  <CustomTextField
            margin="normal"
            fullWidth
            label={label}
            {...props}
            {...field}
            error={(meta.error !== undefined && meta.touched)}
            style={props.disabled ? {backgroundColor:'rgb(222, 222, 222)'}:{}}
        >
            {props.data.map((option) => (
            <MenuItem key={option} value={option}>
              {option}
            </MenuItem>
          ))}
        </CustomTextField>
            <ErrorMessage name={props.name} component="span" style={{ color: theme.palette.valen.error, fontSize: '12px' }} />
        </>

    )
}
