
import { TextField } from '@mui/material';
import { ErrorMessage, useField } from 'formik'
import React from 'react'
import { theme } from '../../configtheme';
import { styled } from '@mui/material/styles';
import { NumericFormat } from 'react-number-format';


const CustomTextField = styled(TextField)`
    & label span{
        color:red!important
    }
    & input:disabled{
        color:red!important;
        -webkit-text-fill-color: rgba(0, 0, 0, 1.0);
    }
`
const NumberFormatCustom = React.forwardRef(function NumberFormatCustom(props, ref) {
    const { onChange, ...other } = props;
    return (
        <NumericFormat
        {...other}
        getInputRef={ref}
        onValueChange={(values) => {
          onChange({
            target: {
              name: props.name,
              value: values.value,
            },
          });
        }}
        thousandSeparator
        valueIsNumericString
      />
    );
});



export const InputNumber = ({ label, ...props }) => {
    const [field, meta] = useField(props);
    return (
        <>  <CustomTextField
            margin="normal"
            fullWidth
            label={label}
            {...props}
            {...field}
            error={(meta.error !== undefined && meta.touched)}
            InputProps={{
                inputComponent: NumberFormatCustom,
                inputProps:{...props}
            }}
        />
            <ErrorMessage name={props.name} component="span" style={{ color: theme.palette.valen.error, fontSize: '12px' }} />
        </>

    )
}

