import { fetchToken } from "../../helpers/fetch";
import { uiConstants } from "../constants/ui.constanst";
import { formConstants } from "../constants/form.constants";
import { PrestamoConstants } from "../constants/prestamo.constants";
import moment from "moment";

export const CreatePrestamo =({id_cliente,cliente,fecha_prestamo,valor_prestamo,tasa_interes,periodo_pago,observacion})=>{
    return async(dispatch)=>{
        try {
            dispatch({ type: uiConstants.uiOpenLoading });
            fecha_prestamo = moment(fecha_prestamo).format();  
            const resp = await fetchToken('prestamos/CreatePrestamo',{id_cliente,cliente,fecha_prestamo,valor_prestamo,tasa_interes,periodo_pago,observacion},'POST');
            const body = await resp.json();
            if(body.ok){
                dispatch({ type: formConstants.formSave});
                dispatch({ type: PrestamoConstants.Create, payload: body.response });
                dispatch({ type: uiConstants.uiErrorMessage, payload: null });
            }else{
                dispatch({ type: uiConstants.uiErrorMessage, payload: body.msg });
            }
            dispatch({ type: uiConstants.uiCloseLoading });

        } catch (error) {
            dispatch({ type: uiConstants.uiCloseLoading });
            dispatch({ type: uiConstants.uiErrorMessage, payload: JSON.stringify(error) })
        }
    }
}

export const UpdatePrestamo = ({_id,id_cliente,cliente,fecha_prestamo,valor_prestamo,tasa_interes,periodo_pago,observacion})=>{
    return async(dispatch)=>{
        try {
            dispatch({ type: uiConstants.uiOpenLoading });
            fecha_prestamo = moment(fecha_prestamo).format();  
            const resp = await fetchToken('prestamos/UpdatePrestamo/'+_id,{id_cliente,cliente,fecha_prestamo,valor_prestamo,tasa_interes,periodo_pago,observacion},'PUT');
            const body = await resp.json();
            if (body.ok) {
                dispatch({ type: formConstants.formSave});
                dispatch({ type: PrestamoConstants.Create, payload: body.response });
                dispatch({ type: uiConstants.uiErrorMessage, payload: null });
            }
            else {
                dispatch({ type: uiConstants.uiErrorMessage, payload: body.msg });
            }
            dispatch({ type: uiConstants.uiCloseLoading });
        } catch (error) {
            console.error(error);
            dispatch({ type: uiConstants.uiCloseLoading });
            dispatch({ type: uiConstants.uiErrorMessage, payload: JSON.stringify(error) })
        }
    }
}

export const DeleteCliente = (_id)=>{
    return async(dispatch)=>{
        try {
            dispatch({ type: uiConstants.uiOpenLoading });
            const resp = await fetchToken('prestamos/DeletePrestamo/'+_id,{},'delete');
            const body = await resp.json();
            if (body.ok) {
                dispatch({ type: formConstants.formDelete});
                dispatch({ type: PrestamoConstants.Delete, payload: body.response });
                dispatch({ type: uiConstants.uiErrorMessage, payload: null });
            }
            else {
                dispatch({ type: uiConstants.uiErrorMessage, payload: body.msg });
            }
            dispatch({ type: uiConstants.uiCloseLoading });
        } catch (error) {
            console.error(error);
            dispatch({ type: uiConstants.uiCloseLoading });
            dispatch({ type: uiConstants.uiErrorMessage, payload: JSON.stringify(error) })
        }
    }
}

export const GetPrestamosByCliente = (id_cliente) =>{
    return async (dispatch)=>{
        try {
            dispatch({ type: uiConstants.uiOpenLoading });
            const resp = await fetchToken('prestamos/getPrestamoByCliente/'+id_cliente,{});
            const body = await resp.json();
            if (body.ok) {
                dispatch({ type: PrestamoConstants.searchByClientId, payload: body.response });
                dispatch({ type: uiConstants.uiErrorMessage, payload: null });
            }
            else {
                dispatch({ type: uiConstants.uiErrorMessage, payload: body.msg });
            }
            dispatch({ type: uiConstants.uiCloseLoading });
        } catch (error) {
            console.error(error);
            dispatch({ type: uiConstants.uiCloseLoading });
            dispatch({ type: uiConstants.uiErrorMessage, payload: JSON.stringify(error) })
        }
    }
}

export const InitPrestamo = ()=>{
    return (dispatch)=>{
        dispatch({ type: PrestamoConstants.Initial})
    }
}

export const selectPrestamo = (data)=>{
    return (dispatch)=>{
        dispatch({ type: PrestamoConstants.Read, payload:data})
    }
}