import React, { useEffect, useState } from 'react'
import { theme } from '../../configtheme';
import * as Yup from 'yup';
import parse from 'date-fns/parse';
import { useDispatch, useSelector } from 'react-redux';
import { formCancel, formFetched, formInit, formNew } from '../../redux/actions/form.actions';
import { uiCleanQuickSearch, uiSetQuickSearchId, uiShowQuickSearch } from '../../redux/actions/ui.actions';
import { Form, Formik } from 'formik';
import { Box, Divider, Grid, SpeedDialIcon, Typography } from '@mui/material';
import { CircularButton } from '../../components/buttons/CircularButton';
import CreditScoreIcon from '@mui/icons-material/CreditScore';
import { InputText } from '../../components/inputs/InputText';
import { GridSearchIcon } from '@mui/x-data-grid';
import CloseIcon from '@mui/icons-material/Close';
import { Loading } from '../../components/Loadings/Loading';
import ErrorAlert from '../../components/Alerts/ErrorAlert';
import { SearchModal } from '../../components/QuickSearch/SearchModal';
import { ClientesColumn, PrestamosColumn } from '../../components/QuickSearch/QsColumns';
import DeleteIcon from '@mui/icons-material/Delete';
import { SaveOutlined } from '@mui/icons-material';
import { InputBr } from '../../components/inputs/InputBr';
import { InputSelect } from '../../components/inputs/InputSelect';
import { InputNumber } from '../../components/inputs/InputNumber';
import { CreatePrestamo, DeleteCliente, UpdatePrestamo } from '../../redux/actions/prestamo.actions';
import moment from 'moment';


const fabStyle = {
    // position: 'fixed',
    color: 'white',
    backgroundColor: `${theme.palette.valen.secondary}`,
    fontSize: '24px',
};
const fabStyle2 = {
    // position: 'fixed',
    color: 'white',
    backgroundColor: `${theme.palette.valen.error}`,
    fontSize: '24px',
};

const initialValues = {
    id_cliente: '',
    cliente: '',
    fecha_prestamo: '',
    valor_prestamo: '',
    tasa_interes: '',
    periodo_pago: '',
    observacion: ''
}
const periodo_pagos = [
    'Mensual',
    'Quincenal'
];

const validation = Yup.object({
    cliente: Yup.string().required('El Cliente es requerido'),
    fecha_prestamo: Yup.date()
        .typeError("please enter a valid date")
        .required('La fecha de prestamo es requerida')
        .min("1969-11-13", "Fecha Invalida"),
    valor_prestamo: Yup.number().required('El valor de prestamo debe ser requerido'),
    tasa_interes: Yup.number().required('la tasa de interes debe ser requerido'),
    periodo_pago: Yup.string().required('El periodo debe ser requerido'),
    observacion: Yup.string(),
})

const Prestamo = () => {
    const dispatch = useDispatch();
    const { cancel, newE, disabled, save, canDelete } = useSelector(state => state.form);
    const [formValues, setFormValues] = useState(null);
    const { quickSearchSelected, quickSearchId } = useSelector(state => state.ui);

    useEffect(() => {
        if (quickSearchSelected) {
            if (quickSearchId === 'cliente') return getDataBrCliente();
            if (quickSearchId === 'prestamo') return loadData();
            //return loadData();
        }
        setFormValues(null);
    }, [quickSearchSelected])

    useEffect(() => {
        dispatch(formInit());
        dispatch(uiCleanQuickSearch());
    }, [dispatch])

    const handleNew = (e) => {
        dispatch(formNew());
        dispatch(uiCleanQuickSearch());
    }
    const handleCancel = (e) => {
        dispatch(formCancel());
        dispatch(uiCleanQuickSearch());
    }

    const handleSubmit = (data) => {
        if (data._id) {
            return dispatch(UpdatePrestamo(data))
        }
        dispatch(CreatePrestamo(data))
    };

    const handleSearch = () => {
        dispatch(uiSetQuickSearchId('prestamo'))
        dispatch(uiShowQuickSearch());
    }

    const handleBrCliente = () => {
        dispatch(uiSetQuickSearchId('cliente'))
        dispatch(uiShowQuickSearch());
    }

    const handleDelete = () => {
        dispatch(DeleteCliente(quickSearchSelected._id))
        dispatch(uiCleanQuickSearch());
    }

    const loadData = () => {
        const {fecha_prestamo} = quickSearchSelected;
        const fecha_formateada =moment(fecha_prestamo).format('yyyy-MM-DD');  
        setFormValues({...quickSearchSelected,fecha_prestamo:fecha_formateada});
        dispatch(formFetched());
    };

    const getDataBrCliente = () => {
        setFormValues({ ...initialValues, id_cliente: quickSearchSelected._id, cliente: quickSearchSelected.nombre + " " + quickSearchSelected.apellido })
    }

    return (
        <>
            <Formik
                initialValues={formValues || initialValues}
                onSubmit={(values) => {
                    handleSubmit(values);
                }}
                validationSchema={validation}
                enableReinitialize
            >
                {
                    ({ handleReset, isValid, errors, resetForm }) => (
                        <Form>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <Typography variant="h6" gutterBottom>
                                    Prestamo
                                </Typography>
                                {newE && (
                                    <>
                                        <CircularButton type="reset" text="Agregar" Icon={SpeedDialIcon} handleClick={() => { handleNew(); handleReset(); }} />
                                        <CircularButton type="button" text="Buscar" Icon={GridSearchIcon} handleClick={handleSearch} />
                                    </>
                                )}
                                {cancel && <CircularButton type="reset" text="Cancelar" Icon={CloseIcon} handleClick={() => { handleCancel(); handleReset(); }} />}
                                <CreditScoreIcon />
                            </div>
                            <Divider />
                            <Grid container spacing={1}>
                                <Grid item xs={12} md={6}>
                                    <InputBr
                                        handleBr={handleBrCliente}
                                        disabled={true}
                                        btn_disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="cliente"
                                        label="Cliente"
                                        name="cliente"
                                    />
                                </Grid>
                                <Grid item xs={12} md={3}>
                                    <InputText
                                        type='date'
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        InputLabelProps={{ shrink: true }}
                                        id="fecha_prestamo"
                                        label="Fecha Prestamo"
                                        name="fecha_prestamo"
                                    />
                                </Grid>
                                <Grid item xs={12} md={3}>
                                    <InputNumber
                                        disabled={disabled}
                                        margin="dense"
                                        id="valor_prestamo"
                                        label="Valor Prestamo"
                                        name="valor_prestamo"
                                        prefix="$"
                                    />
                                </Grid>
                                <Grid item xs={12} md={3}>
                                    <InputNumber
                                        disabled={disabled}
                                        margin="dense"
                                        id="tasa_interes"
                                        label="Tasa Interes"
                                        name="tasa_interes"
                                    />
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <InputSelect
                                        data={periodo_pagos}
                                        select
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="periodo_pago"
                                        label="Periodo Pago"
                                        name="periodo_pago"
                                    />
                                </Grid>
                                <Grid item xs={12} md={5}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="observacion"
                                        label="Observaciones"
                                        name="observacion"
                                    />
                                </Grid>
                            </Grid>

                            <Box>
                                <div style={{ display: 'flex', justifyContent: 'space-around', marginTop: '10px' }}>
                                    {
                                        canDelete &&
                                        <CircularButton size="large" style={fabStyle2} type="button" text="Eliminar" Icon={DeleteIcon} handleClick={() => { handleDelete(); handleReset(); }} />
                                    }
                                    {
                                        save &&
                                        <CircularButton size="large" style={fabStyle} type="submit" text="Guardar Cambios" Icon={SaveOutlined} />
                                    }
                                </div>


                            </Box>
                            {/* <FloatButton handleSave={handleSave} handleSearch={handleSearch} handleDelete={handleDelete} /> */}
                        </Form>
                    )
                }
            </Formik>
            <Loading />
            <ErrorAlert />
            {
                quickSearchId === 'prestamo' && <SearchModal title="busqueda de prestamos" api="prestamos/getPrestamo" columns={PrestamosColumn} />
            }
            {
                quickSearchId === 'cliente' && <SearchModal title="busqueda de clientes" api="clientes/getCliente" columns={ClientesColumn} />
            }
        </>
    )
}

export default Prestamo
